:root {
  font-family: Helvetica, Arial, sans-serif;
  font-size: calc(.6rem + 1vw); }

h2 {
  font-size: calc(1.5rem + 1vw); }

.cardTitle {
  color: #fff; }

.modal__content {
  line-height: 1.5em;
  font-size: calc(1.15rem + 1vw); }

.modal {
  z-index: 20;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  width: 290px;
  max-width: 100%;
  padding: 1.5em;
  background: #eee;
  border-radius: 10px; }
  @media (min-width: 400px) {
    .modal {
      width: 375px; } }
  @media (min-width: 768px) {
    .modal {
      width: 600px; } }
  @media (min-width: 992px) {
    .modal {
      width: 900px; } }
  @media (min-width: 1200px) {
    .modal {
      padding: .5em;
      width: 1200px; } }

.modal > h2 {
  padding: 1rem;
  margin: 0; }

.modal__content {
  padding: 1rem; }

.toggleBar {
  padding: 0.5rem 1rem; }
  .toggleBar__button {
    display: block;
    width: 5em;
    height: 5em;
    border-radius: 50%;
    margin: 0 auto;
    border: 0;
    background: #5d4586;
    padding: 0.5rem 1rem;
    cursor: pointer; }
    .toggleBar__button:hover {
      background: #53318c; }

.scroll-lock {
  overflow: hidden; }

.modal--overlay {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: .75; }

.root {
  position: relative; }
  .root > article {
    background: #5d4586;
    cursor: pointer; }
    .root > article:hover {
      background: #53318c; }
      .root > article:hover > div > img {
        transform: scale(0.85);
        transition: transform .5s ease-in-out; }
    .root > article > h2 {
      margin: 0;
      padding: .5em 0;
      text-align: center; }
    .root > article > div {
      background: #eee; }
      .root > article > div > img {
        display: block;
        margin: 0 auto;
        padding: 1em 0;
        height: 200px; }
        @media (min-width: 1200px) {
          .root > article > div > img {
            height: 350px; } }
  @media (min-width: 768px) {
    .root > article:nth-child(2n+1) {
      border-right: 1px solid #53318c; } }
  @media (min-width: 1200px) {
    .root > article:nth-child(2n+1) {
      border-right: none; } }
  @media (min-width: 1200px) {
    .root > article:not(:nth-child(3n)) {
      border-right: 1px solid #4d2c91; } }

